
  .swiper {
    width: 95vw;
    height: 80vh;
  }
  
  .swiper-slide {
    text-align: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }
  
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .swiper__div {
      position: absolute;
      align-self: center;
      padding: 10px 20px;
      background-color: rgba($color: #000000, $alpha: .7);
      color: $text-color;
  }